<template>
  <div>
    <LeftMenu @updatePosts="openAutor"></LeftMenu>
    <div class="main-wrapper ">

      <section class="cta-section theme-bg-light py-5">
        <div class="container text-center single-col-max-width">
          <h2 class="heading">{{ autorSelected }}</h2>
          <div class="intro text-muted">Posts</div>
        </div>
      </section>

      <div class="row " v-if="posts.length > 0">

        <div class="col-sm-12 col-md-6" v-for="post in posts" :key="post.Id">

          <div class="card w-100 m-2">

            <img :src="$constants.APP_URL + 'storage/posts/' + post.imagen" :alt="post.titulo" class="card-img-top"
              width="100%" height="auto">
            <div class="card-body">
              <h5 class="card-title">{{ post.titulo }}</h5>
              <p class="card-text">{{ post.contenido }}</p>
              <b>Autor:</b> {{ post.autor }}
              <br>
              <b>Fecha:</b> {{ post.created_at }}
            </div>
          </div>

        </div>

      </div>
      <span v-else>Sin posts ...</span>

    </div>

  </div>

</template>

<script>
import LeftMenu from './components/LeftMenu.vue'

export default {
  name: 'App',
  components: {
    LeftMenu
  },
  data(){
    return{
      posts:[],
      autorSelected:"Todos los autores",
      autorElegido:"todos"
    }
  },
  methods:{
    openAutor(_id,_name=''){

      this.autorElegido=_id;

      this.autorSelected=_name;
      if( this.autorSelected== '' )this.autorSelected='Todos los autores';
     
      console.log( "autor en app ", this.autorSelected );

      this.posts=[];
      const _token = document.head.querySelector('meta[name="csrf-token"]');
      let _headers = { 'Content-Type': 'application/json', 'Accept': 'application/json' };
      if( _token != null ){
        _headers = { 'Content-Type': 'application/json', 'Accept': 'application/json','X-CSRF-TOKEN': _token.content };
      }
      this.$axios.post("/post/list", {autor:this.autorElegido}, { headers: _headers }).then(_response => {
        
        if( _response.data.done ){
          this.posts=_response.data.data;          
        }else{
          this.posts=[];
          this.$bootbox.alert({
            message: _response.data.msg,
            size: 'small',
            centerVertical: true,
          });
        }
        
              
      }).catch(error => {
        
        this.$bootbox.alert({
          message: "Error al intentar cargar posts",
          size: 'small',
          centerVertical: true,
        });

        this.response_errors = error.response?.data || "Error desconocido";

      });

    }//fin de abrir autor
  },
  mounted(){
    setTimeout(() => {
      this.openAutor("todos");
    }, 500);

  }
}
</script>

