import Vue from 'vue'
import App from './App.vue'

import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import axios from 'axios';
import bootbox from 'bootbox';
// import store from './store'; // Importamos el store

// Vue.use(BootstrapVue);
Vue.config.productionTip = false;

Vue.prototype.$constants = Object.freeze({
  APP_URL: 'https://test-blog.bitmo.co/',
  API_URL: 'https://test-blog.bitmo.co/api/',
  APP_NAME: 'Blog Manager',
  VERSION: '0.0.1'
});

Vue.prototype.$axios = axios.create({
  baseURL: Vue.prototype.$constants.API_URL,
  timeout: 10000,
  headers: { 'Content-Type': 'application/json' }
});

Vue.prototype.$bootbox = bootbox;

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
