<template>
  <header class="header text-center">
    <h1 class="blog-name pt-lg-4 mb-0"><a class="no-text-decoration" href="index.html">Variedades blog</a></h1>

    <nav class="navbar navbar-expand-lg navbar-dark">

      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navigation"
        aria-controls="navigation" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div id="navigation" class="collapse navbar-collapse flex-column">
        <div class="profile-section pt-3 pt-lg-0">          
          <div class="bio mb-1">Hola, gracias por visitar este maravilloso blog de variedades</div>
        </div>

        <div class="w-100 bg-white shadow-sm rounded-3 mx-3 bg-transparent">
          
          <div class="d-flex p-2 justify-content-between border-bottom ">
            <div class="fw-bold text-dark" >
              <a class="text-dark" href="javascript:void(0)" @click="$emit('updatePosts', 'todos','')">Ver todos</a>
            </div>
            <div class="text-secondary"></div>
          </div>
          
          <div class="d-flex p-2 justify-content-between border-bottom " v-for="autor in autores" :key="autor.Id">
            <div class="fw-bold text-dark">
              <a class="text-dark" href="javascript:void(0)" @click="$emit('updatePosts', autor.Id,autor.nombre)">
                {{autor.nombre}}
              </a>
            </div>
            <div class="text-secondary">
              <img :src="$constants.APP_URL +'storage/autores/' + autor.foto" :alt="autor.nombre" class="rounded-circle" width="24" height="24">
            </div>
          </div>
          
        </div>

       
      </div>
    </nav>
  </header>
</template>

<script>
export default {
  name: 'HelloWorld',
  data(){
    return{
      autores:[]
    }
    
  },
  methods:{
    loadAutores(){
     
      const _token = document.head.querySelector('meta[name="csrf-token"]');
      let _headers = { 'Content-Type': 'application/json', 'Accept': 'application/json' };
      if( _token != null ){
        _headers = { 'Content-Type': 'application/json', 'Accept': 'application/json','X-CSRF-TOKEN': _token.content };
      }

      this.$axios.get("/autor/list",{ headers: _headers }).then(_response => {          
        this.autores = _response.data.data;                
      }).catch(_error => {
        console.error("Error al cargar lista de autores:");
        console.log( _error );
      });
      
    }
  },
  mounted(){
    setTimeout(() => {
      this.loadAutores();
    }, 500);
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .bg-transparent{
    background-color: transparent !important;
  }
</style>